<script>
import { useQuasar } from "quasar";
import { ref } from "vue";
import Header from "../../components/Header.vue";

const setup = {
  origin: process.env.VUE_APP_URL_API_ORIGIN,
  url: process.env.VUE_APP_URL_API_PETS
};

export default {
  name: "Home",
  search: "Pesquisar",
  components: {
    Header,
  },
  setup() {

    const $q = useQuasar();
    const submitting = ref(false);

    const optionsDog = [
      {label:"Vira-lata", value: 1},
      {label:"Shih tzu",value:2},
      {label:"Yorkshire",value:3},
      {label:"Poodle",value:4},
      {label:"Lhasa Apso",value:5},
      {label:"Buldogue Francês",value:6},
      {label:"Golden Retriever",value:7},
      {label:"Labrador",value:8},
      {label:"Maltês",value:9},
      {label:"Pinscher",value:10}
    ];
    const optionsCat = [
      {label:"Elfo",value:11},
      {label:"Bambino",value:12},
      {label:"Lykoi/Lobo",value:13},
      {label:"Ragamuffin",value:14},
      {label:"Caracat",value:16},
      {label:"Maine Coon",value:17},
      {label:"Khao Manee",value:18},
      {label:"Bobtail",value:19},
      {label:"Singapura",value:20},
      {label:"Cymric",value:21}
    ];
    const date = ref("2019/03/01");
    const proxyDate = ref("2019/03/01");

    return {
      setup: setup,
      date,
      proxyDate,
      options:[],
      optionsCat: optionsCat,
      optionsDog: optionsDog,
      updateProxy() {
        proxyDate.value = date.value;
      },

      save() {
        date.value = proxyDate.value;
      },
      submitting,
      pageTitle: "Alterar cadastro",
      pageSubTitle: "Pessoa autorizada",
      showNotif(msg, color, icon) {
        $q.notify({
          message: msg,
          color: color,
          icon: icon,
        });
      },
    };
  },
  data() {
    return {
      transaction: null,
      cid: null,
      bid: null,
      name: null,
      weight: null,
      avatar: null,
      date_of_birth: null,
      gender: null,
      type: null,
      status: null,
      breed: []
    };
  },
  methods: {
async getpet() {
      const req = await fetch(this.setup.url +'/'+ this.$route.params.id);
      this.data = await req.json();
      
      this.pet = this.data.data.pet;
      this.client = this.data.data.client;

      (this.id = this.pet.id),
      (this.cid = this.client.id),      
      (this.name = this.pet.name),
      (this.avatar = this.pet.avatar),
      (this.weight = this.pet.weight),
      (this.date_of_birth = this.pet.date_of_birth),
      (this.gender = this.pet.gender),
      (this.type = this.pet.type),
      (this.status = this.pet.status),
      (this.options = this.pet.type == 'canine' ? this.optionsDog : this.optionsCat)

      const output =  this.options.filter(option => option.value == this.pet.bid);

      this.bid = output[0];

    },
    async createpet() {
      this.submitting = true;

      console.log("salvo");
      const data = {  
        transaction: 'update',
        id: this.id,      
        cid: this.cid,
        bid: this.bid.value,
        name: this.name,
        weight: this.weight,
        avatar: this.avatar??'https://agpublick.com.br/invet/images/pets/avatar-'+this.type+'.png',
        date_of_birth: this.date,
        gender: this.gender,
        type: this.type,
        status: this.status
      };

      const dataJson = JSON.stringify(data);

      console.log(data);

      if (data.name && data.cid && data.weight) {
        const req = await fetch(this.setup.url +'/'+ this.$route.params.id, {
          method: "POST",
          mode: 'cors',
          cache: 'no-cache',
          credentials: 'same-origin',
          headers: {
            'Content-Type': 'application/json'
          },  
          redirect: 'follow',
          referrerPolicy: 'no-referrer',
          body: dataJson

        });

        const res = await req.json();
        console.log(res);

        if (res) {
            this.id = "";
            this.cid = "";
            this.bid = "";
            this.name = "";          
            this.weight = "";
            this.avatar = "";
            this.date_of_birth = "";
            this.gender = "";
            this.type = "";
            this.status = "";

          this.showNotif(
            "Cadastro realizado com sucesso!",
            "green",
            "las la-thumbs-up"
          );
          this.$router.go(-1);
        } else {
          this.showNotif(
            "Ooops! Algo inesperado ocorreu. Tente mais tarde!",
            "red",
            "las la-bomb"
          );
        }
      } else {
        this.showNotif(
          "Por favor. Os campos com * são obrigatórios.",
          "red",
          "las la-exclamation-triangle"
        );
      }
      this.submitting = false;
    },
  },

  mounted() {
    this.getpet();
  },
};
</script>
